.Agreement {
  width: 100%;
  height: 51vh;
}

.Agreement .agreement-content {
  width: 80%;
  margin: 0 auto;
  text-align: left;
  margin-top: 2rem;
  margin-bottom: 2rem;
  text-align: justify;
}

.Agreement .agreement-content a {
  background-color: #db511f;
  width: 200px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  margin: 0 auto;
  text-decoration: none;
  color: white;
  border-radius: 5px;
  margin-top: 2rem;
}

.Agreement .container {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
}

.Agreement .container ul,
.Agreement .container ol {
  padding-left: 1.2rem;
  margin-bottom: 1.5rem;
}

.Agreement .container ul li {
}

.Agreement .agreement-content h2 {
  text-transform: uppercase;
  margin-bottom: 1.5rem;
  font-size: 16px;
}

.Agreement .agreement-content h2 span {
  font-weight: normal;
}

.Agreement .agreement-content p {
  margin-bottom: 1.3rem;
  font-size: 14px;
  line-height: 24px;
}
